@import url("https://fonts.googleapis.com/css2?family=Satoshi:wght@500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-satoshi: Satoshi;
  --font-inter: Inter;

  /* font sizes */
  --font-size-2xl-6: 21.6px;
  --font-size-mid-8: 17.8px;
  --font-size-sm-8: 13.8px;

  /* Colors */
  --color-black: #000;
  --color-darkslategray: #404040;
  --color-aliceblue: #eaf0f7;
  --color-dimgray: #4f555a;
  --color-mediumslateblue: #4461f2;
  --color-white: #fff;
  --color-gainsboro-100: #dfdfdf;

  /* Border radiuses */
  --br-3xs-9: 9.9px;
}
